/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  transition: all 0.3s;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
.contenedor h2 {
  font-weight: 800;
}

.contenido-centrado h1 {
  font-weight: 600;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  transition: all 0.2s;
  border: none;
}
.boton-amarillo:hover {
  background-color: rgb(174.9699570815, 105.4506437768, 7.0300429185);
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  transition: all 0.2s;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: rgb(174.9699570815, 105.4506437768, 7.0300429185);
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  transition: all 0.2s;
  border: none;
}
.boton-verde:hover {
  background-color: rgb(80.4406779661, 126, 0);
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgba(255, 0, 0, 0.662);
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  transition: all 0.2s;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: rgba(204, 0, 0, 0.662);
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  transition: all 0.2s;
  border: none;
}
.boton-verde-block:hover {
  background-color: rgb(80.4406779661, 126, 0);
  cursor: pointer;
}

.dark-mode {
  background-color: rgb(25.5, 25.5, 25.5);
  transition: all 0.3s;
}
.dark-mode b {
  color: #FFFFFF;
}
.dark-mode p, .dark-mode a, .dark-mode h1, .dark-mode h3, .dark-mode h4 {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio, .dark-mode .resumen-propiedad, .dark-mode .resumen-entrada {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio, .dark-mode .resumen-propiedad .precio, .dark-mode .resumen-entrada .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono, .dark-mode .resumen-propiedad .icono, .dark-mode .resumen-entrada .icono {
  filter: invert(1);
}
.dark-mode .resumen-entrada, .dark-mode .resumen-propiedad {
  padding: 2rem;
  border-radius: 1rem;
}
.dark-mode .formulario legend, .dark-mode .formulario label, .dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea, .dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.dark-mode table.propiedades tbody tr td {
  color: #FFFFFF;
  border-color: #333333;
}
.dark-mode .modal .modal-contenido h2 {
  font-weight: bold;
}
.dark-mode .modal .modal-contenido h2, .dark-mode .modal .modal-contenido p {
  color: #333333;
}
.dark-mode .contenido-centrado h1 {
  font-weight: 600;
}

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 45rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.dark-mode-button {
  width: 4rem;
  margin-left: 2.5rem;
  filter: invert(1);
  border-radius: 4rem;
  padding: 0.3rem;
  transition: all 0.3s;
}
.dark-mode-button:hover {
  cursor: pointer;
  filter: invert(0);
  background-color: rgba(255, 255, 255, 0.75);
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra a img {
  margin-bottom: 2rem;
  width: auto;
  height: 50px;
}
@media (min-width: 768px) {
  .barra a img {
    margin-bottom: 0;
    padding-bottom: 0.1rem;
  }
}

.admin {
  background-color: rgba(113, 177, 0, 0.4);
}
.admin:hover {
  background-color: rgba(113, 177, 0, 0.7);
}

.admin, .cerrar-sesion {
  padding: 0.1rem 1rem;
  border-radius: 1.5rem;
  transition: 0.3s ease;
  font-family: "Roboto Flex", sans-serif;
  font-variation-settings: "wght" 300;
  letter-spacing: 0px;
  cursor: pointer;
}
.admin:hover, .cerrar-sesion:hover {
  color: #FFFFFF !important;
  font-variation-settings: "wght" 500;
  letter-spacing: 0.5px;
}

.cerrar-sesion {
  background-color: rgba(255, 0, 0, 0.4);
}
.cerrar-sesion:hover {
  background-color: rgba(255, 0, 0, 0.6);
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.3s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  transition: all 0.3s;
}
.navegacion a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
  cursor: pointer;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.fade-out {
  opacity: 0;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: rgb(237.75, 237.75, 237.75);
  border: 1px solid rgb(212.25, 212.25, 212.25);
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  padding: 0;
  list-style: none;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  margin-top: 1rem;
  width: 50%;
  height: 0.4rem;
  background-color: #71B100;
  transition: all 0.3s;
}
.texto-entrada h4:hover::after {
  width: 100%;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .testimonial p {
    padding-right: 4rem;
  }
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario h4 {
  margin: 0.5rem;
  font-size: 2rem;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario .textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

/** P√°ginas Internas **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenido-nosotros h4 {
  margin: 3rem 0 0 0;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 0.5rem;
  transition: all 0.5s ease-out;
}
.alerta.error {
  background-color: rgba(255, 0, 0, 0.662);
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 3rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 1rem;
}
table.propiedades .imagen-tabla {
  width: 15rem;
  margin: 0 auto;
}
table.propiedades tbody tr td {
  border-bottom: 2px;
  border-color: #e1e1e1;
  border-bottom-style: solid;
  text-align: center;
}

.imagen-small {
  width: 30rem;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
}

.modal-contenido {
  background: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}